<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/messageSet/captcha/list">验证码</cms-tab>
      <cms-tab to="/messageSet/systemTpl/list">系统提示信息</cms-tab>
      <cms-tab to="/messageSet/platformTpl/list">平台消息模板</cms-tab>
      <cms-tab to="/messageSet/userTpl/list">用户消息模板</cms-tab>
      <cms-tab to="/messageSet/shopTpl/list">商家消息模板</cms-tab>
    </cms-tabs>
    <cms-prompt :collapse='false'>
      <li>设置对用户发送消息的内容、方式</li>
      <li>邮箱、短信、推送途径是否发送还需取决于邮箱设置、短信设置及推送设置中是否开启，只有当两者都开启的情况下系统才能正常发送</li>
    </cms-prompt>
    <!-- 操作区 -->
    <cms-sticky>
      <section class="cms-batch" style="border:0;padding-top:1px;">
        <cms-button v-prem="'/messageSet/userTpl/save'" icon="add" type="success" size="small" @click="$routerLink('/messageSet/shopTpl/save','save',{mesType:1})">新增</cms-button>
      </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column prop="mesCode" label="模板标识" min-width='220'>
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.mesCode"> {{scope.row.mesCode}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="mesTitle" label="模板标题" min-width='220'>
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.mesTitle"> {{scope.row.mesTitle}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="模板描述" min-width='300'>
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark"> {{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="details.mesType" label="站内信" min-width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.details&&scope.row.details.length>=1">
            <cms-switch v-model="scope.row.details[0].isOpen"
             @change="setDisable(scope.row.details[0].id,scope.row.details[0].isOpen)"></cms-switch>
          </div>
          <div v-else>
            -
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="details.mesType" label="邮件" min-width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.details&&scope.row.details.length>=2">
            <cms-switch v-model="scope.row.details[1].isOpen" 
             @change="setDisable(scope.row.details[1].id,scope.row.details[1].isOpen)"></cms-switch>
          </div>
          <div v-else>
            -
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="details.mesType" label="短信" min-width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.details&&scope.row.details.length>=3">
            <cms-switch v-model="scope.row.details[2].isOpen"
             @change="setDisable(scope.row.details[2].id,scope.row.details[2].isOpen)"></cms-switch>
          </div>
          <div v-else>
            -
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="details.mesType" label="推送" min-width="120">
        <template slot-scope="scope">
          <div v-if="scope.row.details&&scope.row.details.length>=4">
            <cms-switch v-model="scope.row.details[3].isOpen"
             @change="setDisable(scope.row.details[3].id,scope.row.details[3].isOpen)"></cms-switch>
          </div>
          <div v-else>
            -
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='140' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/messageSet/shopTpl/edit','update',{id:scope.row.id,mesType:1})">编辑</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { messageTpList, updateIsOpen } from '@/api/setting/messageSet.js'

var defaultData = {
  mesType: 1
}
export default {
  name: 'messageSetUserList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    setDisable(id, status) {
      if (status) {
        updateIsOpen({ isOpen: status, id: id }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('开启成功')
          } else {
            this.$errorMessage('开启失败')
          }
        })
      } else {
        updateIsOpen({ isOpen: status, id: id }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('关闭成功')
          } else {
            this.$errorMessage('关闭失败')
          }
        })
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(messageTpList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(messageTpList, this.queryParams)
  }
}
</script>
<style>
</style>



